// ----------------------------------------------------------------------

// Notices (Alerts)

// ----------------------------------------------------------------------

.launchpad-notice {
    padding: 15px;
    width: 100%;
    box-sizing: border-box;

    &.success {
        background-color: #dff0d8;
    }

    &.info {
        background-color: #d9edf7;
    }

    &.danger {
        background-color: #f2dede;
    }
}