// ----------------------------------------------------------------------

// Post Template Styles

// ----------------------------------------------------------------------

.comment-list,
.comment-list ol.children {
	list-style-type: none;
}

.comment-author .avatar {
    float: left;
    margin-right: 0.875em;
}

.bypostauthor {
	opacity: 1;
}

.comment-body {

     > p {
        display: block;
        margin-top: 20px;
    }
}

.comments-title {
    border-top: 1px solid $light-grey;
    padding-top: 2em;
}