/**
 * This file contains the scss for use with the header area
 * of LaunchPad
 */

/**
 * header class
 */
.site-header
{
	background: none;
	display: inline-block;
	width: 100%;
    padding: 40px 0;
}


.site-header
{

	// setup alignment for heard content based on user selected order
	.site-branding,
	.site-navigation .menu-inline {
		float: left;
	}

	.site-navigation + .site-branding {
		float: right;
	}
	.site-branding + .site-navigation .menu-inline {
		float: right;
	}

	// full width columns
	.site-branding.twelve,
	#site-navigation.twelve .menu-inline {
		float: none;
		text-align: center;
		margin-bottom: 0;

		.site-logo {
			margin: 0 auto;
		}

		.site-title,
		.site-description {
			text-align: center;
		}

		@media (#{$bp-larger-than-phablet}) {
			li {
				float: none;
				display: inline-block;
			}
		}
	}

}

/**
 * header id (just in case)
 */
#llp-header
{

}

/**
 * <h1> for site title
 */
.site-title
{
	font-family: 'merriweather sans', sans-serif;
	font-size: 40px;
	font-weight: 800;
	margin: 0;
}

.site-title,
.site-description {
	text-align: center;
	@media (#{$bp-larger-than-phablet}) {
		text-align: left;
	}
}

.site-title a
{
	color: #333;
	text-decoration: none;
}

.site-logo
{
	max-width: 100%;
	margin: 0 auto;
	display: block;
	@media (#{$bp-larger-than-phablet}) {
		margin: 0;
	}
}

/**
 * <p> for site description
 */
.site-description
{
	margin: 0;
	color: #444;
	font-family: 'merriweather', serif;
	font-size: 20px;
}

/**
 * Header widget
 */
#header-widget
{
	float: right;
	padding: 50px 20px;
}