/**
 * This file contains the scss for use with the footer area
 * of LaunchPad
 */

/**
 * Footer class
 */
.site-footer
{
	background: #555;
	padding: 20px;
	color: #C5C5C5;
	font-size: 12px;
	font-weight: 300;
    overflow: hidden;
}

/**
 * Footer id (just in case)
 */
#llp-footer
{

}

/**
 * Footer separator class
 */
.footer-sep
{

}

#footer-widget
{
	background: #B8B8B8;
	padding: 2px 0px 20px 0px;
}

#menu-footer {
	list-style: none;

	li {
		display: inline;
	}

	li + li:before {
		content: '|';
		padding:0 5px;
	}
}
