/**
 * This file contains the scss for use with the navigation area
 * of LaunchPad
 */

#responsive-menu {
    font-size: 1.4rem;
    #menu-header {
        // float: right;

        li {
            margin-right: 20px;
            &:last-child {
                margin-right: 0;
            }
        }

        &.menu-inline {
            // display: inline-block;
            @include clearfix();
        }
    }
}

#mobile-nav-container {
    text-align: center;
    margin-top: 20px;
}

nav#nav-mobile {
    display: block;
    position: relative;
}

nav#nav-mobile ul {
    display: block;
    list-style-type: none;

    li {
        ul {
            display: none;
        }

        &:hover {
            > ul {
                display: block
            }
        }
    }

    //position: absolute;
    //left: 0;
    //right: 0;
    //margin-left: auto;
    //margin-right: auto;
    //text-align: center;
    //background-color: #ff0032;
}
nav#nav-mobile li {
    display: block;
    padding: 5px 0;
    margin: 0;
    border-bottom: solid 1px $light-grey;

    &:hover {
        > ul {
            display: block
        }
    }

    ul {
        display:block;
        position:relative;
        margin: 0;
        padding: 0;
        left:0;

        li {
            float: none;
            width: 100%;

        }
    }

}
nav#nav-mobile li:last-child {
    border-bottom: none;
}
nav#nav-mobile a {
    width: 100%;
    display: block;
    //display: block;
    //color: white;
    padding: 10px 30px;
    font-size: 1.4rem;
}
nav#nav-mobile a:hover {
    background-color: $light-grey;
    //color: #fff;
}



nav#nav-mobile {
    display: none;
}




#site-navigation {

    #responsive-menu {
        display: none;
    }

    .menu-inline {

        ul {
            list-style-type: none;

            li {
                display: block;
                margin-right: 20px;

                &:last-child {
                    margin-right: 0;
                }

                a {
                    font-size: 1.4rem;

                }
            }
        }
    }




}

// For devices larger than 400px
@media (#{$bp-larger-than-phablet}) {

    #mobile-nav-container {
        display: none;
    }

    nav#nav-mobile {
        display: none;
    }

    #responsive-menu-toggle {
        display: none;
    }

    #site-navigation {

        #responsive-menu {
            width: 100%;
            display: block;

            #menu-header {

                ul {

                }
            }
        }

        #nav-mobile {
            display:none;
        }

        .menu-inline {
            list-style-type: none;
            li {
                float: left;
                width: auto;
                position: relative;

                &:hover {
                    > ul
                    {
                        display:block;
                        z-index: 200;
                    }
                }

                ul {
                    display:none;
                    position:absolute;
                    margin: 0;
                    padding: 0;
                    left:0;
                    width:200px;

                    li {
                        float: none;
                        width: 100%;
                        border-bottom: solid 1px $light-grey;
                        background: white;

                        a {
                            padding: 10px;
                            display: block;

                            &:hover {
                                background-color: $light-grey;
                            }

                        }

                        ul {
                            top:0;
                            left:100%
                        }
                    }
                }

            }
        }



    }


}









