.wrap.launchpad {

    #launchpad-settings-tile {
        padding: 20px;
        background: white;
        border-radius: 5px;
        border: 1px solid darken($light-grey, 10%);
        position: relative;

        .launchpad-title {
            border-bottom: 1px solid $light-grey;
        }
    }

    a {
        text-decoration: none;
        color: $link-color;
    }

    // navigation
    #launchpad-settings-nav {

        ul.launchpad-nav-tab-wrapper {
            list-style-type: none;
            margin: 0;
            width: 100%;
            overflow: hidden;
            float: left;
            background: white;
            border-radius: 5px;
            border: 1px solid darken($light-grey, 10%);

            a {
                text-decoration: none;
                display: block;
                padding: 10px;
                color: $black;
                outline: none;
                box-shadow: none;
                outline: 0 !important;
                border: none !important;

                &:hover {
                    background: lighten($light-grey, 4%);
                    color: $link-color;
                    box-shadow: none;
                }
                &:active {
                    outline: 0 !important;
                    border: none !important;
                    box-shadow: none;
                }
                &:focus {
                    outline: 0 !important;
                    border: none !important;
                    box-shadow: none;
                }

                &.launchpad-nav-tab-active {
                    font-weight: bold;
                    background: $light-grey;
                    color: $link-color;
                }

            }

            li {
                float: left;
                position: relative;
                width: 100%;
                border-bottom: 1px solid darken($light-grey, 10%);
                margin-bottom: 0;

                &:last-child {
                    border-bottom: 0;
                }

            }
        }

    }

    fieldset {
        margin: 0;

        ul {
            list-style-type: none;
            margin: 0;
        }
    }

    // form fields
    .launchpad-field-desc {
        width: 100%;
        float: none;
        font-size: .9em;
        color: $grey;
        display: block;
        clear: both;
    }

    input[type="text"].launchpad-field {
        width: 100%;
    }

    select.launchpad-field {
        width: 100%;
    }

    input + .iris-picker {
        margin-top: 0;
    }

    .iris-picker {
        float: left;
        margin-right: 20px;
        margin-top: 0;
        margin-bottom: 14px;
    }

    .launchpad-image-preview {
        position: relative;
        margin-top: 18px;
    }

    .launchpad-image-remove-button {
        background: #fff;
        position: absolute;
        color: #d54e21;
        border: 1px solid #d54e21;
        border-radius: 100%;
        width: 17px;
        height: 17px;
        text-align: center;
        right: -5px;
        top: -5px;

        .fa {
            position: relative;
            top: -1px;
        }

    }

    .launchpad-color-preview {
        margin-top: 10px;
        float: left;
        width: 50px;
        height: 50px;
        border: 1px solid #000000;
    }

    .launchpad-settings-divider {
        margin-top: 20px;
        background: $light-grey;
        padding: 20px;
        color: $dark-grey;
        cursor: pointer;

        p {
            margin: 0;
            padding: 0;
        }

        h1 {
            padding: 0;
            margin: 0;
            color: $dark-grey;
        }
    }

}
