// ----------------------------------------------------------------------

// Lists

// ----------------------------------------------------------------------

ul {
  list-style: circle outside;
}

ol {
  list-style: decimal outside;
  padding-left: 0;
  margin-top: 0;
}

ul {
  padding-left: 0;
  margin-top: 0;
  ul, ol {
    margin: 1.5rem 0 1.5rem 3rem;
    font-size: 90%;
  }
}

ol {
  ol, ul {
    margin: 1.5rem 0 1.5rem 3rem;
    font-size: 90%;
  }
}

li {
  margin-bottom: 1rem;
}

.entry-content {
  ol, ul {
    margin-left: 2rem;
  }
}