// ----------------------------------------------------------------------

// Post Template Styles

// ----------------------------------------------------------------------

.attachment-post-thumbnail {
    max-width: 100%;
    height: auto;
}

.site-content {
    word-wrap: break-word;
}

.site-main > article {
    margin-bottom: 3.5em;
}

.aligncenter {
    display: block;
    float: none;
    margin: 0.375em auto 1.75em;
}

.alignleft {
    float: left;
    margin: 0.375em 1.75em 1.75em 0;
}

.alignright {
    float: right;
    margin: 0.375em 0 1.75em 1.75em;
}

img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}

.wp-caption .wp-caption-text {
    color: $dark-grey;
    font-size: 13px;
    font-size: 0.8125rem;
    font-style: italic;
    line-height: 1.5;
    padding-top: 0.5em;
}

.wp-caption {
    margin-bottom: 1.75em;
    max-width: 100%;
}

blockquote cite:before, blockquote small:before {
    content: "\2014\00a0";
}

blockquote cite, blockquote small {
    display: block;
    line-height: 1.75;
}

pre {
    border: 1px solid #d1d1d1;
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.3125;
    margin: 0 0 1.75em;
    max-width: 100%;
    overflow: auto;
    padding: 1.75em;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.page-links > .page-links-title {
    border: 0;
    height: auto;
    margin: 0;
    padding-right: 0.5em;
    width: auto;
}

.page-links a {
    background-color: $light-grey;
    border-color: $light-grey;
    color: $secondary-color;
}

.page-links a, .page-links > span {
    border: 1px solid $light-grey;
    border-radius: 2px;
    display: inline-block;
    margin-right: 10px;
    text-align: center;
    width: 2em;
}

// For devices larger than 400px
@media (#{$bp-larger-than-phablet}) {
    .site-main > article {
        margin-bottom: 7.0em;
    }
}