// Primary Sidebar and Widgets

// ----------------------------------------------------------------------
#secondary {
    margin-top: 40px;
}
#primary-sidebar {
  aside.widget {
    background: $white;

    h1 {
      font-size: 1.2em;
      background: $primary-color;
      width: 100%;
      text-align: center;
      color: $white;
      padding: 10px 0;
      font-weight: 100;
      margin: 0;
    }

    ul {
      padding: 10px;
    }

    #searchform {
      padding: 10px 10px 0 10px;
    }
  }
}